import { Component } from '@angular/core';
import {
    MemberService,
    RankService,
    AlertService,
} from '../../services/index';
import { BaseComponent } from '../system/base.component';

@Component({
    moduleId: module.id,
    templateUrl: 'member-nonactive.component.html'
})

export class MemberNonactiveComponent extends BaseComponent {
    dd: any = {
        status: [
            { id: 1, name: 'Active' },
            { id: 0, name: 'Nonactive' }
        ],

        gender: [
            { id: 1, name: 'Female' },
            { id: 0, name: 'Male' }
        ],
        married: [
            { id: 1, name: 'Married' },
            { id: 0, name: 'Single' }
        ],

        position: [
            { id: 1, name: 'Left' },
            { id: 2, name: 'Middle' },
            { id: 3, name: 'Right' },
        ],
        member: [],
        rank: []
    };

    constructor(
        private mainService: MemberService,
        private rankService: RankService,
        private alert: AlertService) {
        super(alert);
        this.moduleName = 'Data Member Nonactive';
        this.moduleForm = {
            'id_member': '',
            'username': '',
            'first_name': '',
            'last_name': '',
            'email': '',
            'password': '',
            'birthdate': '',
            'npwp_number': '',
            'is_married': '',
            'gender': '',
            'status': 1,
            'phone_number': '',
            'no_rec': '',
            'position': '',
            'parent_id': '',
            'sponsor_id': '',
            'rank_id': '',
            'created_at': '',
            'updated_at': '',
            'bitrex_cash': '',
            'bitrex_points': '',
            'pv': '',        
        };
        this.moduleFilter = {
            'code': '',
            'name': '',
            'status': 0,
        };
        this.model = mainService;
        this.modulePermission = 'Member.Member.';
    }

    /**
     * function loaddependencies untuk load data service
     */
    loadDependencies() {
        this.rankService.get({}).subscribe(
            (res: any) => {
                this.dd.rank = res.data;
            },
            (err) => {
                this.alert.error('Failed to load data');
            }
        );
    }


    /**
     * Validasi Data Form Utama
     *
     * @returns boolean hasil validasi true/false
     */
    validate() {
      
        return true;
    }


 

}
